.fail {
    .result {
        &__img {
            position: absolute;

            &:nth-child(2) {
                bottom: 0;
                left: 30px;
                width: 10%;
            }

            &:nth-child(3) {
                top: 0;
                right: 0;
                width: 20%;
            }
        }
    }
}