.complete {
    .result {
        &__img {
            position: absolute;

            &:nth-child(2) {
                top: 0;
                left: 0px;
                width: 8%;
            }

            &:nth-child(3) {
                top: 0;
                right: 50px;
                width: 12%;
            }
            &:nth-child(4) {
                top: 15p;
                right: 50px;
                width: 12%;
            }
        }
    }
}


@media (max-width:990px) {}

